close-button {\r\n  background-color: transparent;\r\n  border: 0;\r\n  border-radius: 0.25rem;\r\n  white-space: nowrap;\r\n  text-decoration: none;\r\n  padding: 0.25rem 0.5rem;\r\n  margin: 0.25rem;\r\n  cursor: pointer;\r\n}\r\n.custom-close-button:hover,\r\n.custom-close-button:focus {\r\n    background: transparent;\r\n}\r\n.custom-close-button:focus {\r\n    outline: 1px solid transparent;\r\n    outline-offset: -4px;\r\n}\r\n.custom-close-button:active {\r\n    transform: scale(0.99);\r\n}\r\n.btn-outline-orange {\r\n  border-color: #FF9500;\r\n  color: #1a1a1a;\r\n}\r\n.btn-outline-orange:hover {\r\n  background: #FF9500;\r\n  color: #1a1a1a;\r\n}\r\nbody.dark .btn-outline-orange {\r\n  border-color: #ffa600;\r\n  color: #ffa600;\r\n}\r\nbody.dark .btn-outline-orange:hover {\r\n  background: #FF9500;\r\n  color: #1a1a1a;\r\n}\r\n\r\n/* Secondary buttons */\r\nbody:not(.dark) .btn-secondary {\r\n  background-color: #8B9196;\r\n  border-color: #8B9196;\r\n}\r\nbody:not(.dark) .btn-secondary:hover,\r\nbody:not(.dark) .btn-secondary:active {\r\n  background-color: #6c757d;\r\n  border-color: #6c757d;\r\n}\r\n\r\n/* Small light buttons */\r\n.btn-sm-light {\r\n  padding: 3px 12px;\r\n  font-size: 12px;\r\n  border: 1px solid rgb(27 31 35 / 15%);\r\n  line-height: 20px;\r\n  background-color: #fafbfc;\r\n  transition: .2s cubic-bezier(.3, 0, .5, 1);\r\n  transition-property: color,background-color,border-color;\r\n}\r\n.btn-md-light {\r\n  padding: 4px 8px;\r\n  font-size: 14px;\r\n  line-height: 21px;\r\n}\r\n.btn-sm-light:hover {\r\n  background-color: #f3f4f6;\r\n  border-color: rgb(27 31 35 / 15%);\r\n}\r\nbody.dark .btn-sm-light {\r\n  background-color: #30363d;\r\n  border-color: #8b949e;\r\n  color: #c9d1d9;\r\n}\r\nbody.dark .btn-sm-light:hover {\r\n  background-color: #282e33;\r\n  border-color: #6e7681;\r\n}\r\n.btn-set-width {\r\n  width: 77px;\r\n}\r\n\r\n.text-link {\r\n  color: #e53a23 !important;\r\n}\r\n.text-link:hover {\r\n  color: #FF9500 !important;\r\n}\r\nbody.dark .text-link {\r\n  color: #ffa600 !important;\r\n}\r\nbody.dark .text-link:hover {\r\n  color: #FF9500 !important;\r\n}\r\n\r\n/*--------------------------------------------------------------------------\r\n  Tooltips\r\n---------------------------------------------------------------------------*/\r\n\r\n.tooltip-inner {\r\n  background-color: #f1f1f1 !important;\r\n  color: #1a1a1a;\r\n  text-align: left;\r\n}\r\nbody.dark .tooltip-inner {\r\n  background-color: #000 !important;\r\n  color: #ccc;\r\n}\r\n.btn-tooltip-help {\r\n  height: 0;\r\n  padding: 0;\r\n  transform: translateY(-12px);\r\n}\r\n\r\n/*--------------------------------------------------------------------------\r\n  Images and icons\r\n---------------------------------------------------------------------------*/\r\n\r\n.avatar {\r\n  vertical-align: middle;\r\n  width: 33px;\r\n  height: 33px;\r\n  border-radius: 50%;\r\n}\r\n.feather {\r\n  width: 16px;\r\n  height: 16px;\r\n  vertical-align: text-bottom;\r\n}\r\n.nav-link:after {\r\n  display: none;\r\n}\r\n.primary-icon.feather {\r\n  color: #0BA67F;\r\n}\r\n.error-icon {\r\n  color: #E63F5D;\r\n}\r\n.success-icon {\r\n  color: #0BA67F;\r\n}\r\n.warning-icon {\r\n  color: #ffc107;\r\n}\r\n\r\n/*--------------------------------------------------------------------------\r\n  Navigation\r\n---------------------------------------------------------------------------*/\r\n\r\n.navbar,\r\nbody:not(.dark) #sidebar-menu,\r\nbody:not(.dark) #footer,\r\nbody:not(.dark) #console {\r\n  background-color: #fff !important;\r\n  border-color: #6c757d !important;\r\n}\r\nbody.dark .navbar {\r\n  background-color: rgba(0, 0, 0, .5) !important;\r\n  border-color: #dee2e680 !important;\r\n}\r\n.navbar-brand {\r\n  padding-top: .75rem;\r\n  padding-bottom: .75rem;\r\n  font-size: 1rem;\r\n}\r\n.form-control-dark {\r\n  color: #fff;\r\n  background-color: rgba(255, 255, 255, .1);\r\n  border-color: rgba(255, 255, 255, .1);\r\n}\r\n.form-contro